import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/purchase/purchaseOrder/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";

import {
  Modal,
  Tooltip,
  Table,
  Collapse,
  Select,
  DatePicker,
  Input,
  Row,
  Col,
  Empty
} from "antd";
import { StatusTag, customCollapseStyle, StatusBadge, TransNo } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const Search = Input.Search;
const { getItemRequests } = actions;

const expandedRowRender = data => {
  const columns = [
    {
      title: getLanguage({ id: "no." }),
      dataIndex: "index_no",
      width: "5%",
      align: "right"
    },
    {
      title: getLanguage({ id: "code" }),
      dataIndex: "item_code",
      width: "15%",
      render: (text, row) => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "30%",
      render: (text, row) => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="quantityOnHand" />}>
          <a href="javascript:;"><IntlMessages id="qoh" /> </a>
        </Tooltip>
      ),
      dataIndex: "current_qty",
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.item ? row.item.base_unit.unit_code : null}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="minimumStock" />}>
          <a href="javascript:;"><IntlMessages id="min" /> </a>
        </Tooltip>
      ),
      dataIndex: "min_stock",
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.item ? row.item.base_unit.unit_code : null}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="maximumStock" />}>
          <a href="javascript:;"><IntlMessages id="max" /> </a>
        </Tooltip>
      ),
      dataIndex: "max_stock",
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.item ? row.item.base_unit.unit_code : null}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="requestQuantity" />}>
          <a href="javascript:;"><IntlMessages id="request" /> </a>
        </Tooltip>
      ),
      dataIndex: "request_qty",
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.unit.unit_code}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="sentQuantity" />}>
          <a href="javascript:;"><IntlMessages id="sent" /> </a>
        </Tooltip>
      ),
      dataIndex: "sent_qty",
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.item ? row.item.base_unit.unit_code : null}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="orderedQuantity" />}>
          <a href="javascript:;"><IntlMessages id="ordered" /> </a>
        </Tooltip>
      ),
      dataIndex: "ordered_qty",
      width: "10%",
      align: "right",
      render: (text, row) => <><NumberFormat value={text} /> {row.item ? row.item.base_unit.unit_code : null}</>
    },
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={true}
        style={{ marginTop: "24px" }}
      />
    </div>
  );
};

class ModalItemRequest extends Component {
  state = {
    filter: {
      id_supplier: this.props.id_supplier,
      id_location: this.props.id_location,
      transaction_status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  componentDidUpdate(prevProps) {
    const { id_location } = this.props;

    if (prevProps.id_location !== id_location && id_location) {
      this.handleLocationChange(id_location);
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: id_location
        }
      });
    }
  }

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  // Handle search
  handleSearch = value => {
    const { getItemRequests } = this.props;
    getItemRequests(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    this.setState({
      filter: {
        id_supplier: this.props.id_supplier,
        id_location: this.props.id_location,
        transaction_status: null,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    }, () => this.handleFilter());
  };

  //Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  handleLocationChange = id_location => {
    const { getItemRequests, transaction_date } = this.props;
    const string = `id_location=${id_location}&transaction_date=${transaction_date}`;
    getItemRequests(1, null, string);
  };

  // Handle filter
  handleFilter = () => {
    const { getItemRequests, transaction_date } = this.props;
    //const { item_requests_current_page } = this.props.customer;
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.transaction_status) {
      string
        ? (string += "&transaction_status=" + filter.transaction_status)
        : (string += "transaction_status=" + filter.transaction_status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    } else {
      string
        ? (string +=
            "&transaction_date=" + transaction_date)
        : (string +=
            "transaction_date=" + transaction_date);
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    // console.log(string);
    this.setState({ search: null });
    getItemRequests(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    this.setState({
      filter: {
        id_supplier: this.props.id_supplier,
        id_location: this.props.id_location,
        transaction_status: null,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    }, () => this.handleFilter());
  };

  // Handle pagination
  handlePageChange = page => {
    const { getItemRequests } = this.props;
    const { search, filter } = this.props.purchaseOrder;
    getItemRequests(page, search, filter);
  };

  render() {
    // console.log(this.props);
    const { visible, handleModal, purchaseOrder, transaction_date } = this.props;
    const { itemRequests, locations } = purchaseOrder ? purchaseOrder : [];
    const { loadingForeign, loadingItemRequests } = purchaseOrder
      ? purchaseOrder
      : false;

    const columns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        width: "25%",
        render: (text, row) => {
          let className;
          switch (row.transaction_status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "requested";
              break;
            case 3:
              className = "progress";
              break;
            case 4:
              className = "fulfilled";
              break;
            default:
              className = "new";
              break;
          }
  
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <Tooltip
                placement="bottom"
                title={<IntlMessages id="tooltip.select" />}
              >
                <a
                  onClick={e => {
                    e.preventDefault();
                    this.handleSelect(row.id_item_request);
                  }}
                  href="javascript:;"
                >
                  <TransNo>{text}</TransNo>
                </a>
              </Tooltip>
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "20%",
        render: text => {
          if (text) {
            return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        width: "25%",
        render: text => {
          if (text && text.length > 30) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 30) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "transaction_status",
        rowKey: "transaction_status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 2:
              className = "requested";
              display = getLanguage({ id: "requested" });
              break;
            case 3:
              className = "progress";
              display = getLanguage({ id: "progress" });
              break;
            case 4:
              className = "fulfilled";
              display = getLanguage({ id: "fulfilled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const dataSource = [];
    Object.keys(itemRequests).map((item, index) => {
      return dataSource.push({
        ...itemRequests[item],
        key: item
      });
    });

    const {
      item_requests_current_page,
      item_requests_page_size,
      item_requests_total
    } = this.props.purchaseOrder;

    return (
      <Modal
        title={<IntlMessages id="itemRequests.title" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={handleModal}
        width="95%"
      >
        <Row style={{ marginBottom: "20px" }} type="flex">
          <Col span={18}>
            <Search
              placeholder={
                `${getLanguage({ id: "transactionNo" })}, ` +
                `${getLanguage({ id: "createdBy" })}`
              }
              onSearch={this.handleSearch}
              style={{ width: "99%", height: "36px" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
          </Col>
          <Col span={6}>
            <Button
              type="primary"
              onClick={this.handleSearch.bind(this, this.state.search)}
              style={{ marginRight: "2%" }}
            >
              <IntlMessages id="button.search" />
            </Button>

            <Button onClick={this.handleResetSearch}>
              <IntlMessages id="filter.reset" />
            </Button>
          </Col>
        </Row>
        <Row style={{ marginBottom: "20px" }}>
          <Col>
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row type="flex" style={{ marginBottom: "10px" }}>
                  <Input.Group>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="locations.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_location"
                        )}
                        value={
                          this.state.filter.id_location
                            ? this.state.filter.id_location
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="purchaseOrders.filter.select.location" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        disabled={true}
                        //showArrow={false}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="purchaseOrders.filter.date" />
                      </label>
                      <br />
                      <RangePicker
                        showTime
                        format="DD MMMM YYYY HH:mm:ss"
                        allowClear={true}
                        style={{ width: "100%" }}
                        value={
                          this.state.filter.start_date &&
                          this.state.filter.end_date
                            ? [
                                moment(this.state.filter.start_date),
                                moment(this.state.filter.end_date)
                              ]
                            : []
                        }
                        onChange={this.handleFilterChange.bind(this, "date")}
                        disabledDate={current =>
                          current > moment(transaction_date).endOf("day")
                        }
                      />
                    </Col>
                  </Input.Group>
                </Row>

                <Row type="flex" justify="start">
                  <Col span={24}>
                    <Input.Group style={{ display: "flex" }}>
                      <Col span={4}>
                        <label><IntlMessages id="status" /></label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "transaction_status"
                          )}
                          value={
                            this.state.filter.transaction_status
                              ? this.state.filter.transaction_status
                              : []
                          }
                          style={{ width: "100%" }}
                          placeholder={<IntlMessages id="status" />}
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={2}>{getLanguage({ id: "requested" })}</Option>
                          <Option value={3}>{getLanguage({ id: "progress" })}</Option>
                        </Select>
                      </Col>
                      <Col span={4}>
                        <label>
                          <IntlMessages id="dataPerPage" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "pagination"
                          )}
                          value={
                            this.state.filter.pagination
                              ? this.state.filter.pagination
                              : item_requests_page_size
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value={10}>
                            10
                          </Option>
                          <Option key={2} value={25}>
                            25
                          </Option>
                          <Option key={3} value={50}>
                            50
                          </Option>
                          <Option key={4} value={100}>
                            100
                          </Option>
                        </Select>
                      </Col>

                      <Col
                        span={4}
                        style={{ marginTop: "auto", height: "30px" }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.submit" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Input.Group>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </Col>
        </Row>
        <Row>
          <Col>
            {dataSource.length === 0 && !loadingItemRequests ? (
              <Empty
                description={<IntlMessages id="itemRequests.table.noData" />}
              />
            ) : (
              <Table
                dataSource={dataSource}
                columns={columns}
                loading={loadingItemRequests}
                expandedRowRender={record =>
                  expandedRowRender(record.item_request_details)
                }
                expandIcon={props => (
                  <Button
                    onClick={e => props.onExpand(props.record, e)}
                    size="small"
                  >
                    <IntlMessages id="button.view" />
                  </Button>
                )}
                // Set pagination, jika ada item_requests_page_size mengunakan pagination API
                // jika tidak ada item_requests_page_size menggunakan pagination bawaan
                pagination={
                  item_requests_page_size
                    ? {
                        current: item_requests_current_page,
                        pageSize: parseInt(item_requests_page_size),
                        total: item_requests_total,
                        onChange: this.handlePageChange.bind(this)
                      }
                    : true
                }
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getItemRequests }
  )
)(ModalItemRequest);
