import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PurchaseOrderCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/purchase/purchaseOrder/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";

const redirecTo = "/dashboard/purchases/purchase-orders";

class CreatePurchaseOrder extends Component {
  componentDidMount() {
    const { createPurchaseOrder } = this.props;
    createPurchaseOrder();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.purchaseOrder.error) {
      notification("error", this.props.purchaseOrder.error);
      this.props.clearError();
    }
    if (
      (this.props.purchaseOrder.success || this.props.purchaseOrder.warning) &&
      this.props.purchaseOrder.id_purchase_order
    ) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.purchaseOrder.id_purchase_order
      );
      // this.props.history.replace(redirecTo);
      // notification("success", this.props.purchaseOrder.success);
      // this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { storePurchaseOrder } = this.props;
    storePurchaseOrder(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="purchaseOrders.button.add" />}>
            <Button
              onClick={() => {
                removeSaveData("po");
                this.props.history.replace(redirecTo);
              }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <PurchaseOrderCreateForm
            submit={this.handleSubmit}
            purchaseOrder={this.props.purchaseOrder}
            history={this.props.history}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    purchaseOrder: state.purchaseOrder,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(CreatePurchaseOrder);
