const actions = {
  getPurchaseOrders: (page, search, filter) => ({
    type: "GET_PURCHASE_ORDERS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createPurchaseOrder: () => ({
    type: "CREATE_PURCHASE_ORDER_REQUEST"
  }),
  getItemsBySupplier: (id_supplier, id_location, date, search) => ({
    type: "GET_FILTER_ITEMS_BY_SUPPLIER_PO_REQUEST",
    payload: {
      id_supplier: id_supplier,
      id_location: id_location,
      date: date,
      search: search
    }
  }),
  getDiscounts: data => ({
    type: "GET_DISCOUNT_PO_REQUEST",
    payload: { body: data }
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_FILTER_PURCHASE_ORDERS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getItemRequests: (page, search, filter) => ({
    type: "GET_FILTER_ITEM_REQUESTS_PO_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storePurchaseOrder: data => ({
    type: "STORE_PURCHASE_ORDER_REQUEST",
    payload: { body: data }
  }),
  editPurchaseOrder: (id_purchase_order, search) => ({
    type: "EDIT_PURCHASE_ORDER_REQUEST",
    payload: { id_purchase_order: id_purchase_order, search: search }
  }),
  updatePurchaseOrder: (id_purchase_order, data) => ({
    type: "UPDATE_PURCHASE_ORDER_REQUEST",
    payload: {
      id_purchase_order: id_purchase_order,
      body: data
    }
  }),
  showPurchaseOrder: id_purchase_order => ({
    type: "SHOW_PURCHASE_ORDER_REQUEST",
    payload: { id_purchase_order: id_purchase_order }
  }),
  confirmPurchaseOrder: id_purchase_order => ({
    type: "CONFIRM_PURCHASE_ORDER_REQUEST",
    payload: { id_purchase_order }
  }),
  unconfirmPurchaseOrder: (id_purchase_order, data) => ({
    type: "UNCONFIRM_PURCHASE_ORDER_REQUEST",
    payload: { id_purchase_order, body: data }
  }),
  closePurchaseOrder: (id_purchase_order, data) => ({
    type: "CLOSE_PURCHASE_ORDER_REQUEST",
    payload: { id_purchase_order, body: data }
  }),
  cancelPurchaseOrder: (id_purchase_order, data) => ({
    type: "CANCEL_PURCHASE_ORDER_REQUEST",
    payload: { id_purchase_order, body: data }
  }),
  uploadDocument: (id_purchase_order, data) => ({
    type: "UPLOAD_PURCHASE_ORDER_DOCUMENT_REQUEST",
    payload: {
      id_purchase_order,
      body: data
    }
  }),
  downloadDocument: id_purchase_order_document => ({
    type: "DOWNLOAD_PURCHASE_ORDER_DOCUMENT_REQUEST",
    payload: { id_purchase_order_document }
  }),
  deleteDocument: (id_purchase_order_document, id_purchase_order) => ({
    type: "DELETE_PURCHASE_ORDER_DOCUMENT_REQUEST",
    payload: {
      id_purchase_order_document: id_purchase_order_document,
      id_purchase_order: id_purchase_order
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
