import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/purchase/purchaseOrder/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  InputNumber,
  DatePicker,
  Select,
  Spin,
  Row,
  Col,
  // Button as ButtonAntd,
  Tabs,
  Empty,
  Tooltip,
  Icon,
  Radio,
  Divider,
  Modal
} from "antd";

import EditableTable from "./editableTable";
import ModalTransaction from "./modal";
import ModalItemRequest from "./modalItemRequest";
// import ModalDownPayment from "./modalDownPayment";
// import { Object } from "core-js";
import {
  getTotalTax,
  getExlDiscTax,
  getTotalDiscount,
  getTotalAmount,
  getSupplierDiscount,
  getSaveData,
  setSaveData,
  removeSaveData,
  parseDiscount,
  getRate
} from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import AddModule from "../../../App/AddModule";
// import Popconfirms from "../../../../components/feedback/popconfirm";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const TabPane = Tabs.TabPane;
const confirm = Modal.confirm;

const {
  getTransactions,
  getItemRequests,
  getItemsBySupplier,
  getDiscounts
} = actions;

class PurchaseOrderCreateForm extends Component {
  state = {
    id_location: null,
    id_supplier: null,
    item: null,
    items: [],
    itemDetails: {
      purchase_order_details: []
    },
    poDetails: {
      po_down_payment: {
        id_purchase_order: null,
        id_cash_bank: null,
        id_dp_account: null,
        id_cash_flow_type: null,
        bank_issuer: null,
        reference_no: null,
        dp_due_date: null,
        amount: null
      }
    },
    suppDisc: "0",
    total_qty: 0,
    discount_amount: 0,
    total_tax: 0,
    total_exclude_discount_tax: 0,
    total_discount: 0,
    total_amount: 0,
    search: null,
    visible: false,
    visibleItemReq: false,
    visibleDownPayment: false,
    inclusive_tax: false,
    netPayDay: null,
    netPayDisc: 0,
    err: false,
    err_item: false,
    clear: false,
    restore: null,
    viewed: false,
    import_trans: false
  };

  componentDidMount = () => {
    /* Init Restore Autosave */
    let restore = getSaveData("po");
    if (restore) {
      const net_payment_day = restore.net_payment_day
        ? restore.net_payment_day
        : 0;

      // set trans date ke masa sekarang
      const transDate = moment().format("YYYY-MM-DD");
      const due_date =
        net_payment_day || net_payment_day === 0
          ? moment(transDate).add(net_payment_day, "d")
          : null;

      restore.fields.transaction_date = moment();
      restore.fields.due_date = due_date;
      restore.fields.expired_date = restore.fields.expired_date
        ? moment(restore.fields.expired_date)
        : null;
      restore.fields.delivery_date = restore.fields.delivery_date
        ? moment(restore.fields.delivery_date)
        : null;
      this.setState({ restore: restore });
    }
    /* END Init Restore Autosave */
  };

  componentDidUpdate = (prevProps, prevState) => {
    const {
      itemDetails,
      poDetails,
      total_qty,
      discount_amount,
      total_tax,
      total_exclude_discount_tax,
      total_discount,
      total_amount,
      netPayDay,
      netPayDisc,
      inclusive_tax,
      id_item_request,
      viewed
    } = this.state;
    const { getFieldsValue, getFieldValue } = this.props.form;
    const { prevTransConfirm } = this.props.purchaseOrder;

    /* Show modal transaction yang harus di proses */
    if (prevTransConfirm && !viewed) {
      if (getSaveData("po")) {
        this.setState({ viewed: true });
      } else {
        this.showTransaction();
        this.setState({ viewed: true });
      }
    }

    /* Restore Autosave */
    if (this.state.restore && prevProps.purchaseOrder.loadingForeign) {
      const { restore } = this.state;
      const { setFieldsValue } = this.props.form;
      const { getItemsBySupplier } = this.props;
      setFieldsValue(restore.fields);
      const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD");

      if (restore.fields.id_supplier && restore.fields.id_location) {
        getItemsBySupplier(
          restore.fields.id_supplier,
          restore.fields.id_location,
          transDate
        );
      }

      // console.log(restore);
      this.setState({
        items: restore ? restore.purchase_order_details : [],
        poDetails: {
          po_down_payment: {
            id_purchase_order: restore
              ? restore.po_down_payment
                ? restore.po_down_payment.id_purchase_order
                : null
              : null,
            id_cash_bank: restore
              ? restore.po_down_payment
                ? restore.po_down_payment.id_cash_bank
                : null
              : null,
            id_dp_account: restore
              ? restore.po_down_payment
                ? restore.po_down_payment.id_dp_account
                : null
              : null,
            id_cash_flow_type: restore
              ? restore.po_down_payment
                ? restore.po_down_payment.id_cash_flow_type
                : null
              : null,
            bank_issuer: restore
              ? restore.po_down_payment
                ? restore.po_down_payment.bank_issuer
                : null
              : null,
            reference_no: restore
              ? restore.po_down_payment
                ? restore.po_down_payment.reference_no
                : null
              : null,
            dp_due_date: restore
              ? restore.po_down_payment
                ? restore.po_down_payment.dp_due_date
                : null
              : null,
            amount: restore
              ? restore.po_down_payment
                ? restore.po_down_payment.amount
                : null
              : null
          }
        },
        inclusive_tax: restore ? restore.inclusive_tax : [],
        suppDisc: restore ? restore.fields.discount : [],
        netPayDay: restore ? restore.net_payment_day : 0,
        netPayDisc: restore ? restore.net_payment_discount : 0,
        id_location: restore ? restore.fields.id_location : null,
        id_supplier: restore ? restore.fields.id_supplier : null,
        restore: null
      });
    }
    /* End Restore Autosave */

    /* Auto Save */
    let data = Object.assign(
      {},
      { fields: getFieldsValue() },
      itemDetails,
      poDetails,
      {
        total_qty: total_qty,
        discount_amount: discount_amount,
        total_tax: total_tax,
        total_exclude_discount_tax: total_exclude_discount_tax,
        total_discount: total_discount,
        total_amount: total_amount,
        net_payment_day: netPayDay,
        net_payment_discount: netPayDisc,
        inclusive_tax: inclusive_tax,
        id_item_request: id_item_request
      }
    );

    data.fields.transaction_date = data.fields.transaction_date
      ? data.fields.transaction_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    data.fields.due_date = data.fields.due_date
      ? data.fields.due_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    data.fields.expired_date = data.fields.expired_date
      ? data.fields.expired_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    data.fields.delivery_date = data.fields.delivery_date
      ? data.fields.delivery_date.format("YYYY-MM-DD HH:mm:ss")
      : null;

    if (
      itemDetails.purchase_order_details !==
      prevState.itemDetails.purchase_order_details
    ) {
      setSaveData("po", data);
    }

    if (poDetails.po_down_payment !== prevState.poDetails.po_down_payment) {
      setSaveData("po", data);
    }

    /* END Autosave */

    /* Ubah item list setelah dapat discount */
    if (
      prevProps.purchaseOrder.itemDiscount !==
      this.props.purchaseOrder.itemDiscount
    ) {
      this.setState({
        items: this.props.purchaseOrder.itemDiscount,
        clear: this.props.purchaseOrder.itemDiscount.length === 0 ? true : false
      });
    }

    /* Get Discount setelah klik import */
    if (
      this.state.id_supplier &&
      !prevState.visibleItemReq &&
      !this.state.visibleItemReq &&
      prevState.items.length > 0 &&
      this.state.itemDetails.purchase_order_details.length > 0 &&
      (prevProps.purchaseOrder.itemDiscount !==
        this.props.purchaseOrder.itemDiscount ||
        this.props.purchaseOrder.itemDiscount.length === 0)
    ) {
      this.getDiscountItemList(false);
    }

    /* Get Discount setelah ganti supplier */
    if (
      this.state.id_supplier !== prevState.id_supplier &&
      this.state.itemDetails.purchase_order_details.length > 0
    ) {
      this.getDiscountItemList(false);
    }

    /* Get Discount setelah ganti location */
    if (
      this.state.id_location !== prevState.id_location &&
      this.state.itemDetails.purchase_order_details.length > 0
    ) {
      this.getDiscountItemList(true);
    }
  };

  // Show transaction yang harus diproses
  showTransaction = () => {
    const { prevTransConfirm } = this.props.purchaseOrder;

    confirm({
      title: prevTransConfirm.title,
      content: prevTransConfirm.content,
      icon: <Icon type="info-circle" />,
      okText: prevTransConfirm.ok_button,
      cancelText: prevTransConfirm.cancel_button,
      onOk: () => {
        this.handleModalItemRequest();
      },
      onCancel() {}
    });
  };

  // confirm jika item exist di PO
  processItem = item => {
    const { processItemConfirm } = this.props.purchaseOrder;

    confirm({
      title: processItemConfirm.title,
      content: processItemConfirm.content,
      icon: <Icon type="info-circle" />,
      okText: processItemConfirm.ok_button,
      cancelText: processItemConfirm.cancel_button,
      onOk: () => {
        const { search } = this.state;
        const { getItemsBySupplier } = this.props;
        const { getFieldValue, getFieldsValue } = this.props.form;
        const { id_supplier, id_location } = getFieldsValue([
          "id_supplier",
          "id_location"
        ]);

        this.setState({
          item: item
        });

        if (search) {
          const transDate = getFieldValue("transaction_date").format(
            "YYYY-MM-DD"
          );
          getItemsBySupplier(id_supplier, id_location, transDate);
          this.setState({ search: null });
        }
      },
      onCancel() {}
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const {
        itemDetails,
        poDetails,
        total_qty,
        discount_amount,
        total_tax,
        total_exclude_discount_tax,
        total_discount,
        total_amount,
        netPayDay,
        netPayDisc,
        inclusive_tax,
        id_item_request
      } = this.state;

      // validasi item
      if (itemDetails.purchase_order_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      if (err) {
        this.setState({ err: true });
        return;
      }

      values.transaction_date = values.transaction_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );
      values.delivery_date = values.delivery_date
        ? values.delivery_date.format("YYYY-MM-DD HH:mm:ss")
        : null;
      values.due_date = values.due_date
        ? values.due_date.format("YYYY-MM-DD HH:mm:ss")
        : null;
      values.expired_date = values.expired_date
        ? values.expired_date.format("YYYY-MM-DD HH:mm:ss")
        : null;
      // values.supplier_credit_limit = values.supplier_credit_limit
      //   ? parseFloat(values.supplier_credit_limit.replace(/\$\s?|(,*)/g, ""))
      //   : 0;
      values.discount = values.discount ? values.discount : 0;

      const po_deliv = {
        po_delivery: {
          delivery_date: values.delivery_date ? values.delivery_date : null,
          expired_date: values.expired_date ? values.expired_date : null,
          ship_to: values.ship_to ? values.ship_to : null
        }
      };

      const data = Object.assign({}, values, itemDetails, poDetails, po_deliv, {
        total_qty: total_qty,
        discount_amount: discount_amount,
        total_tax: total_tax,
        total_exclude_discount_tax: total_exclude_discount_tax,
        total_discount: total_discount,
        total_amount: total_amount,
        net_payment_day: netPayDay,
        net_payment_discount: netPayDisc,
        inclusive_tax: inclusive_tax,
        id_item_request: id_item_request
      });
      // console.log(data);
      if (!err) {
        this.props.submit(data);
      }
    });
  };

  getDiscountItemList = changed => {
    const { getFieldsValue } = this.props.form;
    const {
      itemDetails,
      poDetails,
      netPayDay,
      netPayDisc,
      inclusive_tax,
      id_item_request,
      total_qty,
      discount_amount,
      total_tax,
      total_exclude_discount_tax,
      total_discount,
      total_amount
    } = this.state;

    let values = getFieldsValue();

    values.transaction_date = values.transaction_date.format(
      "YYYY-MM-DD HH:mm:ss"
    );
    values.delivery_date = values.delivery_date
      ? values.delivery_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    values.due_date = values.due_date
      ? values.due_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    values.expired_date = values.expired_date
      ? values.expired_date.format("YYYY-MM-DD HH:mm:ss")
      : null;
    // values.supplier_credit_limit = values.supplier_credit_limit
    //   ? parseFloat(values.supplier_credit_limit.replace(/\$\s?|(,*)/g, ""))
    //   : 0;
    const po_deliv = {
      po_delivery: {
        delivery_date: values.delivery_date ? values.delivery_date : null,
        expired_date: values.expired_date ? values.expired_date : null,
        ship_to: values.ship_to ? values.ship_to : null
      }
    };

    let modified = { purchase_order_details: [] };
    if (changed) {
      let index_no = 1;
      itemDetails.purchase_order_details.map(data => {
        if (!data.id_item_request_detail) {
          modified.purchase_order_details.push(
            Object.assign({}, data, { index_no: index_no })
          );
          index_no++;
        }
      });
    } else {
      modified.purchase_order_details = [...itemDetails.purchase_order_details];
    }

    const data = Object.assign({}, values, modified, poDetails, po_deliv, {
      total_qty: total_qty,
      discount_amount: discount_amount,
      total_tax: total_tax,
      total_exclude_discount_tax: total_exclude_discount_tax,
      total_discount: total_discount,
      total_amount: total_amount,
      net_payment_day: netPayDay,
      net_payment_discount: netPayDisc,
      inclusive_tax: inclusive_tax,
      id_item_request: id_item_request
    });
    // console.log(data);
    this.props.getDiscounts(data);
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleModalItemRequest = () => {
    const { visibleItemReq } = this.state;
    visibleItemReq
      ? this.setState({ visibleItemReq: false })
      : this.setState({ visibleItemReq: true });
  };

  handleModalDownPayment = () => {
    const { visibleDownPayment } = this.state;
    visibleDownPayment
      ? this.setState({ visibleDownPayment: false })
      : this.setState({ visibleDownPayment: true });
  };

  handleSelectLocation = id_location => {
    const { setFieldsValue } = this.props.form;

    this.setState({
      id_location: id_location,
      // id_supplier: null,
      clear: false
    });

    setFieldsValue({
      id_supplier: undefined,
      id_payment_category: undefined,
      id_payment_term: undefined,
      due_date: undefined
    });
  };

  handleSelectItem = value => {
    if (value) {
      const { items } = this.props.purchaseOrder;
      const { search } = this.state;
      const { getItemsBySupplier } = this.props;
      const { getFieldValue, getFieldsValue } = this.props.form;
      const { id_supplier, id_location } = getFieldsValue([
        "id_supplier",
        "id_location"
      ]);
      const item = items.find(item => item.id_item === value);
      if (!item.is_exist) {
        this.setState({
          item: item
        });

        if (search) {
          const transDate = getFieldValue("transaction_date").format(
            "YYYY-MM-DD"
          );
          getItemsBySupplier(id_supplier, id_location, transDate);
          this.setState({ search: null });
        }
      } else {
        this.processItem(item);
      }
    }
  };

  handleSelectModal = value => {
    const { transactions } = this.props.purchaseOrder;
    const { setFieldsValue } = this.props.form;
    const { id_supplier } = this.state;
    let transaction = transactions.find(
      curr_data => curr_data.id_purchase_order === value
    );
    transaction.purchase_order_details.map(data => {
      data.id_item_request = null;
      data.id_item_request_detail = null;
      data.item_request_no = null;
      data.received_qty = 0;
      data.id_project = null;
      data.id_department = null;
      data.item_note = null;
      data.id_purchase_order = null;
      data.id_purchase_order_detail = null;
    });

    this.setState({
      items: transaction.purchase_order_details,
      // itemDetails: {
      //   purchase_order_details: transaction.purchase_order_details
      // },
      id_location: transaction.id_location,
      id_supplier: transaction.id_supplier,
      inclusive_tax: transaction.inclusive_tax ? true : false,
      netPayDay: transaction.net_payment_day,
      netPayDisc: transaction.net_payment_discount,
      import_trans: id_supplier ? true : false
    });
    setFieldsValue({
      id_location: transaction.id_location,
      id_supplier: transaction.id_supplier,
      inclusive_tax: transaction.inclusive_tax
    });
    this.handleModal();
  };

  handleSelectModalItemRequest = value => {
    const { itemRequests } = this.props.purchaseOrder;
    const { setFieldsValue } = this.props.form;
    const { itemDetails, id_supplier } = this.state;
    let itemRequest = itemRequests.find(
      curr_data => curr_data.id_item_request === value
    );

    const find = itemDetails.purchase_order_details.findIndex(
      currData => currData.id_item_request === itemRequest.id_item_request
    );

    if (find < 0) {
      itemRequest.item_request_details.map(
        data => (data.item_request_no = itemRequest.transaction_no)
      );
      let items = [];
      [
        ...itemDetails.purchase_order_details,
        ...itemRequest.item_request_details
      ].map(data => items.push(Object.assign({}, data)));

      items.map((data, index) => (data.index_no = index + 1));
      // console.log(items);
      this.setState({
        items: items,
        id_location: itemRequest.id_location,
        clear: false,
        import_trans: id_supplier ? true : false
        // id_item_request: [...id_item_request, itemRequest.id_item_request]
      });
    }
    // else if(find) {
    //   const updateData = itemDetails.purchase_order_details.filter(
    //     data => data.id_item_request !== itemRequest.id_item_request
    //   );
    //   let items = [...updateData, ...itemRequest.item_request_details];
    //   items.map((data, index) => (data.index_no = index + 1));

    //   this.setState({
    //     items: items,
    //     id_location: itemRequest.id_location,
    //     clear: false
    //     // id_item_request: [...id_item_request, itemRequest.id_item_request]
    //   });
    // }
    setFieldsValue({ id_location: itemRequest.id_location });
    this.handleModalItemRequest();
  };

  handleChangeTransDate = status => {
    if (!status) {
      const { setFieldsValue, getFieldValue, getFieldsValue } = this.props.form;
      const { getItemsBySupplier, getItemRequests } = this.props;
      const { id_supplier, id_location } = getFieldsValue([
        "id_supplier",
        "id_location"
      ]);
      const { paymentTerms, currencies } = this.props.purchaseOrder;
      const paymentTerm = paymentTerms.find(
        data => data.id_payment_term === getFieldValue("id_payment_term")
      );

      let currRate = 1;
      let fiscRate = 1;
      if (getFieldValue("id_currency")) {
        const currency = currencies.find(
          data => data.id_currency === getFieldValue("id_currency")
        );
        const rate = getRate(
          currency.currency_rate,
          getFieldValue("transaction_date")
        );
        currRate = rate.currency_rate;
        fiscRate = rate.fiscal_rate;
      }

      const transDateTime = getFieldValue("transaction_date").format(
        "YYYY-MM-DD HH:mm:ss"
      );
      const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD");
      const due_date = paymentTerm
        ? moment(transDateTime).add(paymentTerm.net_payment_day, "d")
        : null;
      setFieldsValue({
        due_date: due_date,
        currency_rate: currRate ? currRate : 1,
        fiscal_rate: fiscRate ? fiscRate : 1
      });
      if (id_location) {
        const filterDate = moment(transDateTime)
          .endOf("day")
          .format("YYYY-MM-DD HH:mm:ss");
        const filter = `id_location=${id_location}&transaction_date=${filterDate}`;
        getItemRequests(1, null, filter);
      }
      if (id_location && id_supplier) {
        // console.log("masuk");
        getItemsBySupplier(id_supplier, id_location, transDate);
        if (this.state.itemDetails.purchase_order_details.length > 0) {
          this.getDiscountItemList(false);
        }
      }
    }
  };

  handleChangeSupplier = id_supplier => {
    if (id_supplier) {
      const { setFieldsValue, getFieldValue, getFieldsValue } = this.props.form;
      const { getItemsBySupplier } = this.props;
      const { currencies, suppliers, paymentTerms } = this.props.purchaseOrder;
      const { id_location } = getFieldsValue(["id_location"]);
      const supplier = suppliers.find(data => data.id_supplier === id_supplier);
      const currency = currencies.find(
        data => data.id_currency === supplier.id_currency
      );
      const paymentTerm = paymentTerms.find(
        data => data.id_payment_term === supplier.id_payment_term
      );
      const rate = getRate(
        currency.currency_rate,
        getFieldValue("transaction_date")
      );
      const currRate = rate.currency_rate;
      const fiscRate = rate.fiscal_rate;
      const transDateTime = getFieldValue("transaction_date").format(
        "YYYY-MM-DD HH:mm:ss"
      );
      const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD");
      const due_date = paymentTerm
        ? moment(transDateTime).add(paymentTerm.net_payment_day, "d")
        : null;
      const disc = parseFloat(supplier.discount) * 100 + "%";

      if (id_location) {
        getItemsBySupplier(id_supplier, id_location, transDate);
      }

      this.setState({
        id_supplier: id_supplier,
        netPayDay: paymentTerm ? paymentTerm.net_payment_day : 0,
        netPayDisc: paymentTerm ? paymentTerm.discount : 0,
        clear: false
      });
      const e = {
        target: { value: disc }
      };
      this.handleChangeSuppDisc(e);
      setFieldsValue({
        supplier_credit_limit: supplier
          ? parseFloat(supplier.supplier_credit_limit)
              .toFixed(2)
              .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
          : 0,
        id_currency: supplier ? supplier.id_currency : null,
        currency_rate: currRate ? currRate : 1,
        fiscal_rate: fiscRate ? fiscRate : 1,
        id_payment_category: supplier ? supplier.id_payment_category : null,
        id_payment_term: supplier ? supplier.id_payment_term : null,
        due_date: due_date,
        discount: disc
      });
    }
  };

  handleChangePaymentTerm = id_payment_term => {
    const { setFieldsValue, getFieldValue } = this.props.form;
    const { paymentTerms } = this.props.purchaseOrder;
    const paymentTerm = paymentTerms.find(
      data => data.id_payment_term === id_payment_term
    );
    const transDate = getFieldValue("transaction_date").format(
      "YYYY-MM-DD HH:mm:ss"
    );
    const due_date = paymentTerm
      ? moment(transDate).add(paymentTerm.net_payment_day, "d")
      : null;
    this.setState({
      netPayDay: paymentTerm.net_payment_day,
      netPayDisc: paymentTerm.discount
    });
    setFieldsValue({ due_date: due_date });
  };

  handleInclusiveTax = e => {
    this.setState({ inclusive_tax: e.target.value });
  };

  handleSearchItem = value => {
    const { getItemsBySupplier } = this.props;
    const { getFieldValue, getFieldsValue } = this.props.form;
    const { id_supplier, id_location } = getFieldsValue([
      "id_supplier",
      "id_location"
    ]);
    // console.log(this.props.purchaseInvoice);
    this.setState({ search: value });
    if (id_supplier && id_location) {
      const transDate = getFieldValue("transaction_date").format("YYYY-MM-DD");
      getItemsBySupplier(id_supplier, id_location, transDate, value);
    }
  };

  handleChangeItem = value => {
    this.setState({ search: value });
  };

  handleChangeSuppDisc = e => {
    const { itemDetails, inclusive_tax } = this.state;
    const { purchase_order_details } = itemDetails;
    const { setFieldsValue } = this.props.form;
    let value = "0";
    if (e.target.value && e.target.value.slice(-1) === "%") {
      value = `${parseDiscount(e.target.value)}%`;
    } else {
      value = parseDiscount(e.target.value);
    }

    this.setState({
      suppDisc: value,
      discount_amount: getSupplierDiscount(purchase_order_details, value),
      total_tax: getTotalTax(purchase_order_details, value, inclusive_tax),
      total_exclude_discount_tax: getExlDiscTax(
        purchase_order_details,
        inclusive_tax,
        value
      ),
      total_discount: getTotalDiscount(
        purchase_order_details,
        value,
        inclusive_tax
      ),
      total_amount: getTotalAmount(purchase_order_details, value, inclusive_tax)
    });
    setFieldsValue({ discount: value });
  };

  handleSaveItemList = (items, total_qty) => {
    const { suppDisc, inclusive_tax } = this.state;
    this.setState({
      itemDetails: { purchase_order_details: items },
      total_qty: total_qty,
      discount_amount: getSupplierDiscount(items, suppDisc),
      total_tax:
        items.length > 0 ? getTotalTax(items, suppDisc, inclusive_tax) : 0,
      total_exclude_discount_tax:
        items.length > 0 ? getExlDiscTax(items, inclusive_tax, suppDisc) : 0,
      total_discount: getTotalDiscount(items, suppDisc, inclusive_tax),
      total_amount: getTotalAmount(items, suppDisc, inclusive_tax),
      clear: false
    });
  };

  // handleOkDownPayment = value => {
  //   const { setFieldsValue } = this.props.form;
  //   setFieldsValue({ amount: value.amount });
  //   value.amount = value.amount
  //     ? parseFloat(value.amount.replace(/\$\s?|(,*)/g, ""))
  //     : null;

  //   this.setState({
  //     poDetails: {
  //       po_down_payment: {
  //         ...this.state.poDetails.po_down_payment,
  //         ...value
  //       }
  //     }
  //   });
  //   this.handleModalDownPayment();
  // };

  clearItem = (items, total_qty) => {
    const { suppDisc, inclusive_tax } = this.state;
    this.setState({
      itemDetails: { purchase_order_details: items },
      item: null,
      total_qty: total_qty,
      discount_amount: getSupplierDiscount(items, suppDisc),
      total_tax:
        items.length > 0 ? getTotalTax(items, suppDisc, inclusive_tax) : 0,
      total_exclude_discount_tax:
        items.length > 0 ? getExlDiscTax(items, inclusive_tax, suppDisc) : 0,
      total_discount: getTotalDiscount(items, suppDisc, inclusive_tax),
      total_amount: getTotalAmount(items, suppDisc, inclusive_tax)
    });
  };

  clearItems = (items, total_qty) => {
    const { suppDisc, inclusive_tax, import_trans } = this.state;
    this.setState(
      {
        itemDetails: { purchase_order_details: items },
        items: [],
        total_qty: total_qty,
        discount_amount: getSupplierDiscount(items, suppDisc),
        total_tax:
          items.length > 0 ? getTotalTax(items, suppDisc, inclusive_tax) : 0,
        total_exclude_discount_tax:
          items.length > 0 ? getExlDiscTax(items, inclusive_tax, suppDisc) : 0,
        total_discount: getTotalDiscount(items, suppDisc, inclusive_tax),
        total_amount: getTotalAmount(items, suppDisc, inclusive_tax),
        clear: false
      },
      () => {
        if (import_trans) {
          this.setState({ import_trans: false });
          return this.getDiscountItemList(false);
        }
      }
    );
  };

  render() {
    // console.log(this.state.itemDetails);
    const { search, visible, visibleItemReq } = this.state;
    const {
      getFieldDecorator,
      getFieldValue,
      setFieldsValue
    } = this.props.form;
    const { purchaseOrder } = this.props;
    const {
      locations,
      suppliers,
      currencies,
      paymentCategories,
      paymentTerms,
      projects,
      departments,
      couriers,
      items,
      taxes
      // cashBanks,
      // accounts,
      // dpAccount,
      // cashFlowTypes
    } = purchaseOrder ? purchaseOrder : [];
    const { user } = this.props;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingTransactions,
      loadingItemRequests,
      loadingForeign,
      loadingTable
    } = purchaseOrder ? purchaseOrder : [];
    // const data = purchaseOrder.purchaseOrder ? purchaseOrder.purchaseOrder : [];

    // const used = data.is_used;
    const error = purchaseOrder
      ? purchaseOrder.errorField
        ? purchaseOrder.errorField
        : []
      : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalTransaction
          visible={visible}
          purchaseOrder={purchaseOrder}
          handleModal={this.handleModal}
          handleSelectModal={this.handleSelectModal}
          id_location={getFieldValue("id_location")}
          id_supplier={getFieldValue("id_supplier")}
        />

        <ModalItemRequest
          visible={visibleItemReq}
          purchaseOrder={purchaseOrder}
          id_location={getFieldValue("id_location")}
          transaction_date={
            getFieldValue("transaction_date")
              ? moment(getFieldValue("transaction_date"))
                  .endOf("day")
                  .format("YYYY-MM-DD HH:mm:ss")
              : moment()
                  .endOf("day")
                  .format("YYYY-MM-DD HH:mm:ss")
          }
          handleModal={this.handleModalItemRequest}
          handleSelectModal={this.handleSelectModalItemRequest}
        />

        {/* <ModalDownPayment
          visible={visibleDownPayment}
          purchaseOrder={{ purchaseOrder: this.state.poDetails }}
          cashBanks={cashBanks}
          cashFlowTypes={cashFlowTypes}
          id_location={getFieldValue("id_location")}
          currency_rate={getFieldValue("currency_rate")}
          total_amount={this.state.total_amount}
          accounts={accounts}
          dpAccount={dpAccount}
          transaction_status={4}
          transDate={getFieldValue("transaction_date")}
          handleModal={this.handleModalDownPayment}
          submit={this.handleOkDownPayment}
        /> */}

        <Tabs defaultActiveKey="1">
          <TabPane
            tab={<IntlMessages id="purchaseOrders.tabs.detail" />}
            key="1"
          >
            <Row>
              <Col span={12} style={{ paddingRight: "5px" }}>
                <Col span={24}>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={
                              <IntlMessages id="purchaseOrders.purchaseOrderNo.help" />
                            }
                          >
                            <IntlMessages id="purchaseOrders.copyPreviousPO" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        <Button
                          disabled={
                            loadingTransactions ||
                            !getFieldValue("id_location") ||
                            !getFieldValue("id_supplier")
                          }
                          icon="select"
                          loading={loadingTransactions}
                          onClick={this.handleModal}
                          style={{ height: "31px", width: "100%" }}
                          // shape="circle"
                        >
                          <IntlMessages id="button.copy" />
                        </Button>
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="purchaseOrders.transactionDate" />
                        }
                        style={{ marginBottom: 0, paddingBottom: 0 }}
                      >
                        {getFieldDecorator("transaction_date", {
                          initialValue: moment()
                        })(
                          <DatePicker
                            style={{ width: "100%" }}
                            showTime
                            format="DD MMMM YYYY HH:mm:ss"
                            // onChange={this.handleChangeTransDate}
                            onOpenChange={this.handleChangeTransDate}
                            disabled={
                              !permissions.includes("Can Back Date")
                                ? true
                                : false
                            }
                            allowClear={false}
                            disabledDate={e => e.valueOf() > moment()}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>

                <Col span={24}>
                  <InputGroup>
                    <Col span={16}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="purchaseOrders.location" />}
                      >
                        {getFieldDecorator("id_location", {
                          initialValue: default_employee_location.id_location
                            ? default_employee_location.id_location
                            : undefined,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="purchaseOrders.location" />
                            }
                            loading={loadingForeign}
                            onSelect={this.handleSelectLocation}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {locations.map((data, key) => (
                              <Option key={key} value={data.id_location}>
                                {`${data.location_code} (${data.location_name})`}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={8}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={
                              <IntlMessages id="purchaseOrders.importRQ.help" />
                            }
                          >
                            <IntlMessages id="purchaseOrders.importRQ" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        <Button
                          icon="select"
                          // loading={loadingTransactions}
                          disabled={
                            loadingItemRequests || !getFieldValue("id_location")
                          }
                          onClick={this.handleModalItemRequest}
                          style={{ height: "31px", width: "100%" }}
                          // shape="circle"
                        >
                          <IntlMessages id="button.import" />
                        </Button>
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
              </Col>
              <Col span={12} style={{ paddingLeft: "5px" }}>
                <Form.Item
                  {...formTextArea}
                  label={<IntlMessages id="purchaseOrders.note" />}
                >
                  {getFieldDecorator("note", {
                    // initialValue: data.note
                  })(<TextArea autosize={{ minRows: 4 }} />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={12} style={{ paddingRight: "5px" }}>
                <Col span={24}>
                  <InputGroup>
                    <Col span={16}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="purchaseOrders.supplier" />}
                      >
                        {getFieldDecorator("id_supplier", {
                          // initialValue: user.username,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="purchaseOrders.supplier" />
                            }
                            loading={loadingForeign}
                            onSelect={this.handleChangeSupplier}
                            notFoundContent={
                              getFieldValue("id_location") ? (
                                <Empty
                                  description={<IntlMessages id="noData" />}
                                />
                              ) : (
                                <Empty
                                  description={<IntlMessages id="noLocation" />}
                                />
                              )
                            }
                            optionFilterProp="children"
                            //filterOption={true}
                          >
                            {getFieldValue("id_location")
                              ? AddModule(
                                  "Create Supplier",
                                  "/dashboard/setup/suppliers/add",
                                  "suppliers.pageHeader",
                                  this.props.history,
                                  permissions
                                )
                              : null}

                            {suppliers.map((data, key) =>
                              data.id_location ===
                              getFieldValue("id_location") ? (
                                <Option key={key} value={data.id_supplier}>
                                  {`${data.supplier_code} (${data.supplier_name})`}
                                </Option>
                              ) : null
                            )}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={8}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="purchaseOrders.creditLimit" />}
                      >
                        {getFieldDecorator("supplier_credit_limit", {
                          // initialValue: user ? user.username : null
                        })(
                          <Input
                            disabled={true}
                            //   style={{ textTransform: "uppercase" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
              </Col>
              <Col span={12} style={{ paddingLeft: "5px" }}>
                <InputGroup>
                  <Col span={6}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseInvoices.currency" />}
                    >
                      {getFieldDecorator("id_currency", {
                        // initialValue: data.id_currency
                      })(
                        <Select
                          showSearch
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="purchaseInvoices.currency" />
                          // }
                          loading={loadingForeign}
                          disabled={true}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {currencies.map((data, key) => (
                            <Option key={key} value={data.id_currency}>
                              {data.currency_code}
                            </Option>
                          ))}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={6}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <Tooltip
                          placement="top"
                          title={
                            <IntlMessages id="purchaseInvoices.currencyRate.help" />
                          }
                        >
                          <IntlMessages id="purchaseInvoices.currencyRate" />
                          <Icon
                            style={{ marginLeft: "5px" }}
                            type="question-circle"
                            theme="filled"
                          />
                        </Tooltip>
                      }
                    >
                      {getFieldDecorator("currency_rate", {
                        // initialValue: data.currency_rate
                      })(
                        <InputNumber
                          placeholder={getLanguage({
                            id: "purchaseInvoices.currencyRate"
                          })}
                          formatter={value =>
                            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                          }
                          parser={value => value.replace(/\$\s?|(,*)/g, "")}
                          min={0}
                          style={{ width: "100%" }}
                          disabled={
                            getFieldValue("id_currency") &&
                            currencies.length > 0
                              ? purchaseOrder.currencies.find(
                                  cur =>
                                    cur.id_currency ===
                                    getFieldValue("id_currency")
                                ).is_base === 1
                                ? true
                                : false
                              : false
                          }
                          onBlur={() => this.getDiscountItemList(false)}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={6}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <Tooltip
                          placement="top"
                          title={
                            <IntlMessages id="purchaseInvoices.fiscalRate.help" />
                          }
                        >
                          <IntlMessages id="purchaseInvoices.fiscalRate" />
                          <Icon
                            style={{ marginLeft: "5px" }}
                            type="question-circle"
                            theme="filled"
                          />
                        </Tooltip>
                      }
                    >
                      {getFieldDecorator("fiscal_rate", {
                        // initialValue: data.fiscal_rate
                      })(
                        <InputNumber
                          placeholder={getLanguage({
                            id: "purchaseInvoices.fiscalRate"
                          })}
                          formatter={value =>
                            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                          }
                          parser={value => value.replace(/\$\s?|(,*)/g, "")}
                          min={0}
                          style={{ width: "100%" }}
                          disabled={
                            getFieldValue("id_currency") &&
                            currencies.length > 0
                              ? purchaseOrder.currencies.find(
                                  cur =>
                                    cur.id_currency ===
                                    getFieldValue("id_currency")
                                ).is_base === 1
                                ? true
                                : false
                              : false
                          }
                        />
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>
              </Col>
            </Row>
            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="purchaseOrders.subtitle.items" />
            </Divider>

            <Row style={{ marginBottom: "25px" }}>
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  validateStatus={
                    error.purchase_order_details || this.state.err_item
                      ? "error"
                      : null
                  }
                  help={
                    error.purchase_order_details ? (
                      error.purchase_order_details
                    ) : this.state.err_item ? (
                      <IntlMessages id="itemDataEmpty" />
                    ) : null
                  }
                  extra={<IntlMessages id="purchaseOrders.item.help" />}
                >
                  <Select
                    value={search ? search : []}
                    showSearch
                    allowClear
                    // optionFilterProp="children"
                    filterOption={false}
                    placeholder={
                      <IntlMessages id="purchaseOrders.item.placeholder" />
                    }
                    loading={loadingItems}
                    notFoundContent={
                      loadingItems ? (
                        <Spin size="small" />
                      ) : getFieldValue("id_location") &&
                        getFieldValue("id_supplier") ? (
                        <Empty description={<IntlMessages id="noData" />} />
                      ) : (
                        <Empty
                          description={
                            <IntlMessages id="purchaseInvoices.item.noSelection" />
                          }
                        />
                      )
                    }
                    onSelect={this.handleSelectItem}
                    onChange={this.handleChangeItem}
                    onSearch={this.handleSearchItem}
                    optionFilterProp="children"
                    filterOption={true}
                    value={undefined}
                  >
                    {getFieldValue("id_supplier")
                      ? AddModule(
                          "Create Item",
                          "/dashboard/setup/items/add",
                          "items.pageHeader",
                          this.props.history,
                          permissions
                        )
                      : null}
                    {items.map(data =>
                      getFieldValue("id_supplier") ? (
                        <Option key={data.id_item} value={data.id_item}>
                          {data.item_code + " - " + data.item_name}
                        </Option>
                      ) : null
                    )}
                  </Select>
                </Form.Item>
              </Col>
              <Col span={24}>
                <EditableTable
                  item={this.state.item}
                  items={this.state.items}
                  suppDisc={this.state.suppDisc}
                  currency_rate={getFieldValue("currency_rate")}
                  taxes={taxes}
                  departments={departments}
                  projects={projects}
                  inclusive_tax={this.state.inclusive_tax}
                  transaction_status={4}
                  handleSaveItemList={this.handleSaveItemList}
                  clearItem={this.clearItem}
                  clearItems={this.clearItems}
                  loadingTransactions={loadingTransactions}
                  loadingItems={loadingItems}
                  loadingTable={loadingTable}
                  clear={this.state.clear}
                  permissions={permissions}
                />
              </Col>
              <Col span={12}>
                <Col span={10} style={{ marginTop: "15px", textAlign: "left" }}>
                  <strong>
                    <IntlMessages id="purchaseInvoices.totalQty" />
                  </strong>
                  <strong>
                    <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                  </strong>
                </Col>
                <Col span={13} offset={1} style={{ marginTop: "15px" }}>
                  <strong>
                    <NumberFormat value={this.state.total_qty} />
                  </strong>
                </Col>
              </Col>
            </Row>

            <Row>
              <Col span={14} offset={10} style={{ paddingLeft: "15px" }}>
                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.exlDiscountAndTax" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat
                        value={this.state.total_exclude_discount_tax}
                      />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={10}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.discount" />
                  </Col>
                  <Col span={4} offset={1}>
                    <Tooltip
                      placement="bottom"
                      title={
                        <IntlMessages id="purchaseInvoices.discount.help" />
                      }
                    >
                      {getFieldDecorator("discount", {
                        initialValue: 0
                      })(
                        <Input
                          size="small"
                          disabled={
                            !permissions.includes(
                              "Edit Discount Purchase Order"
                            )
                          }
                          onChange={this.handleChangeSuppDisc}
                          onBlur={() =>
                            setFieldsValue({ discount: this.state.suppDisc })
                          }
                          style={{
                            marginTop: "3px",
                            borderColor:
                              this.state.total_exclude_discount_tax -
                                this.state.discount_amount <
                              0
                                ? "#f5222d"
                                : "#d9d9d9"
                          }}
                        />
                      )}
                    </Tooltip>
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={this.state.discount_amount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.totalDiscount" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={this.state.total_discount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.totalTax" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={this.state.total_tax} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <strong>
                      <IntlMessages id="total" />
                    </strong>
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={this.state.total_amount} />
                    </span>
                  </Col>
                </Row>

                {/* <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <strong>
                      <IntlMessages id="purchaseOrders.downPayment" />
                    </strong>
                  </Col>
                  <Col span={8} offset={1}>
                    {getFieldDecorator("amount", {
                      initialValue: 0
                    })(
                      <Input
                        readOnly={true}
                        onClick={this.handleModalDownPayment}
                        addonAfter={
                          <Tooltip
                            placement="bottom"
                            title={
                              <IntlMessages id="purchaseOrders.setDownPayment" />
                            }
                          >
                            <ButtonAntd
                              style={{
                                width: "100%",
                                height: "30px",
                                border: "0px",
                                backgroundColor: "transparent"
                              }}
                              loading={loadingForeign}
                              disabled={loadingForeign}
                              onClick={this.handleModalDownPayment}
                              icon="plus"
                            />
                          </Tooltip>
                        }
                      />
                    )}
                  </Col>
                </Row> */}
              </Col>
            </Row>
          </TabPane>
          <TabPane
            tab={<IntlMessages id="salesOrders.tab.paymentAndShipment" />}
            key="2"
          >
            <Row>
              <Col span={12} style={{ paddingRight: "5px" }}>
                <Divider
                  orientation="left"
                  style={{ fontSize: 14, marginTop: 0 }}
                >
                  <IntlMessages id="purchaseOrders.tabs.payment" />
                </Divider>
                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <IntlMessages id="purchaseInvoices.paymentCategory" />
                      }
                    >
                      {getFieldDecorator("id_payment_category", {
                        // initialValue: data.id_payment_category,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          showSearch
                          style={{ width: "100%" }}
                          placeholder={
                            <IntlMessages id="purchaseInvoices.paymentCategory" />
                          }
                          loading={loadingForeign}
                          onChange={() =>
                            setFieldsValue({
                              id_payment_term: undefined,
                              due_date: undefined
                            })
                          }
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {paymentCategories.map((data, key) => 
                            data.id_location === getFieldValue("id_location") || 
                            data.id_location === null ? (
                              <Option key={key} value={data.id_payment_category}>
                                {data.payment_category_code}
                              </Option>
                            ) : null
                          )}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseInvoices.paymentTerm" />}
                    >
                      {getFieldDecorator("id_payment_term", {
                        // initialValue: data.id_payment_term,
                        rules: [
                          {
                            required: true,
                            message: <IntlMessages id="required" />
                          }
                        ]
                      })(
                        <Select
                          showSearch
                          style={{ width: "100%" }}
                          onSelect={this.handleChangePaymentTerm}
                          placeholder={
                            <IntlMessages id="purchaseInvoices.paymentTerm" />
                          }
                          loading={loadingForeign}
                          optionFilterProp="children"
                          filterOption={true}
                        >
                          {getFieldValue("id_payment_category")
                            ? paymentTerms.map((data, key) => {
                                const paymentCategory = paymentCategories.find(
                                  data =>
                                    data.id_payment_category ===
                                    getFieldValue("id_payment_category")
                                );
                                if (
                                  paymentCategory.payment_type === 2 &&
                                  data.is_cash === 0
                                ) {
                                  return (
                                    <Option
                                      key={key}
                                      value={data.id_payment_term}
                                    >
                                      {data.payment_term_code}
                                    </Option>
                                  );
                                } else if (
                                  paymentCategory.payment_type !== 2 &&
                                  data.is_cash === 1
                                ) {
                                  return (
                                    <Option
                                      key={key}
                                      value={data.id_payment_term}
                                    >
                                      {data.payment_term_code}
                                    </Option>
                                  );
                                }
                              })
                            : null}
                        </Select>
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>

                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseOrders.dueDate" />}
                    >
                      {getFieldDecorator("due_date", {
                        // initialValue: moment(
                        //   data.payment_due_date,
                        //   "YYYY-MM-DD"
                        // )
                      })(
                        <DatePicker
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY HH:mm:ss"
                          disabled={true}
                          allowClear={false}
                          disabledDate={e => e.valueOf() > moment()}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={6}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseOrders.inclusiveTax" />}
                    >
                      {getFieldDecorator("inclusive_tax", {
                        initialValue: 0
                      })(
                        <Radio.Group
                          onChange={this.handleInclusiveTax}
                          // value={this.state.inclusive_tax}
                          buttonStyle="solid"
                        >
                          <Radio.Button value={0}>
                            <IntlMessages id="no" />
                          </Radio.Button>
                          <Radio.Button value={1}>
                            <IntlMessages id="yes" />
                          </Radio.Button>
                        </Radio.Group>
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>
              </Col>
              <Col span={12} style={{ paddingLeft: "5px" }}>
                <Divider
                  orientation="left"
                  style={{ fontSize: 14, marginTop: 0 }}
                >
                  <IntlMessages id="purchaseOrders.tabs.shipment" />
                </Divider>
                <Col span={24}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="itemTransfers.courier" />}
                  >
                    {getFieldDecorator("id_courier", {
                      // initialValue: data.id_courier,
                    })(
                      <Select
                        showSearch
                        allowClear
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="itemTransfers.courier" />
                        }
                        loading={loadingForeign}
                        optionFilterProp="children"
                        filterOption={true}
                      >
                        {couriers.map((data, key) => (
                          <Option key={key} value={data.id_courier}>
                            {`${data.courier_name} (${data.description})`}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>

                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseOrders.deliveryDate" />}
                    >
                      {getFieldDecorator("delivery_date", {
                        // initialValue: moment()
                      })(
                        <DatePicker
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY HH:mm:ss"
                          allowClear={true}
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="purchaseOrders.expiredDate" />}
                    >
                      {getFieldDecorator("expired_date", {
                        // initialValue: moment()
                      })(
                        <DatePicker
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY HH:mm:ss"
                          allowClear={true}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>

                <Row>
                  <Col span={24}>
                    <Form.Item
                      {...formTextArea}
                      label={<IntlMessages id="purchaseOrders.shipTo" />}
                    >
                      {getFieldDecorator("ship_to", {
                        // initialValue: data.ship_to
                      })(<TextArea autosize={{ minRows: 5 }} />)}
                    </Form.Item>
                  </Col>
                </Row>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            tab={<IntlMessages id="jobCostings.tab.attachment" />}
            key="3"
            disabled
          />
        </Tabs>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "24px" }}
            loading={loading || loadingSubmit}
            onClick={() => removeSaveData("po")}
            disabled={
              this.state.total_exclude_discount_tax -
                this.state.total_discount <
              0
            }
          >
            <IntlMessages id="button.submit" />
          </Button>
          <br />
          {this.state.err ? (
            <span style={{ color: "#f5222d" }}>
              <IntlMessages id="form.error" />
            </span>
          ) : null}

          {this.state.total_exclude_discount_tax - this.state.total_discount <
          0 ? (
            <IntlMessages id="discountExceed">
              {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
            </IntlMessages>
          ) : null}

          {this.state.err_item ? (
            <IntlMessages id="itemDataEmpty">
              {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
            </IntlMessages>
          ) : null}
        </Form.Item>
      </Form>
    );
  }
}

const WrappedPurchaseOrderCreateForm = Form.create({
  name: "create_purchase_order"
})(PurchaseOrderCreateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(mapStateToProps, {
    getTransactions,
    getItemRequests,
    getItemsBySupplier,
    getDiscounts
  })
)(WrappedPurchaseOrderCreateForm);
